(function() {
  'use strict';
  angular
    .module('vmsFrontend')
    .factory('userProfileEndpoint', userProfileEndpoint);

  /** @ngInject */
  function userProfileEndpoint($log, $http, apiBaseUrl) {
    var service = {
      create: create,
      get: get,
      update: update,
      drop: drop,
      getAttendingProjects: getAttendingProjects
    };

    return service;

    function create(user) {
      return $http({
        method: 'POST',
        url: apiBaseUrl + '/register',
        data: user
      });
    }

    function get() {
      return $http({
        method: 'GET',
        url: apiBaseUrl + '/users/me'
      });
    }

    function update(data) {
      return $http({
        method: 'PUT',
        url: apiBaseUrl + '/users/me',
        data: data
      });
    }

    function drop(credentials) {
      /**
       * @TODO: change the POST action into DELETE (?)
       */
      return $http({
        method: 'POST',
        url: apiBaseUrl + '/users/me/delete',
        data: credentials
      });
    }

    function getAttendingProjects(id) {
      return $http({
        method: 'GET',
        url: apiBaseUrl + '/users/' + id + '/attending_projects'
      });
    }
  }

})();

(function() {
  'use strict';
  angular
    .module('vmsFrontend')
    .factory('userProfile', userProfile);

  /** @ngInject */
  function userProfile($log, $q, userProfileEndpoint, vmsLocalStorage) {
    var service = {
      create: create,
      get: get,
      update: update,
      drop: drop,
      getAttendingProjects: getAttendingProjects
    };

    $log.debug('userProfile');

    return service;

    function create(user, role) {
      var deferred = $q.defer();

      userProfileEndpoint
        .create(user)
        .then(function(response) {
          var data = response.data;

          $log.debug(data);

          vmsLocalStorage.setUsername(data.username);
          vmsLocalStorage.setJwt(data.auth_access_token);

          switch (role) {
            case 'volunteer':
              vmsLocalStorage.setRole('volunteer');
              break;
          }

          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function get() {
      var deferred = $q.defer();

      userProfileEndpoint
        .get()
        .then(function(response) {
          var value = response.data;
          var year = value.data.birth_year;
          value.data.birth_year = new Date(year, 1);

          deferred.resolve(value);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function update(data) {
      var deferred = $q.defer();

      if (data.birth_year instanceof Date) {
        var date = data.birth_year;
        data.birth_year = date.getFullYear();
      }

      userProfileEndpoint
        .update(data)
        .then(function(response) {
          var value = response.data;
          var year = value.data.birth_year;
          value.data.birth_year = new Date(year, 1);

          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function drop(credentials) {
      var deferred = $q.defer();

      userProfileEndpoint
        .drop(credentials)
        .then(function(response) {
          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function getAttendingProjects(id) {
      var deferred = $q.defer();

      userProfileEndpoint
        .getAttendingProjects(id)
        .then(function(response) {
          deferred.resolve(response.data.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }
  }

})();

