(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .directive('formatYear', formatYear);

    /** @ngInject */
    function formatYear($filter, $log) {
        return {
            require: 'ngModel',
            link: function(scope, element, attrs, modelCtrl) {

                modelCtrl.$parsers.push(function(inputValue) {

                    var transformedInput = $filter('date')(inputValue, 'yyyy');

                    $log.debug('=== formatYear directive ===');
                    $log.debug(transformedInput);

                    if (transformedInput != inputValue) {
                        modelCtrl.$setViewValue(transformedInput);
                        modelCtrl.$render();
                    }

                    return transformedInput;
                });
            }
        };
    }
})();
(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .filter('formatDateTime', formatDateTime);

  /** @ngInject */
  function formatDateTime($filter, $log) {
    var filter = function(dateString) {
      var dateValue = new Date(dateString);
      var transformedInput = $filter('date')(dateValue, 'yyyy/MM/dd hh:mm', 'UTC');

      $log.debug('=== formatDateTime directive ===');
      $log.debug(transformedInput);

      return transformedInput;
    };

    return filter;
  }
})();

