(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($log, userAuthentication, $rootScope, $state) {
    var vm = this;
    vm.alert = [];

    vm.login = function() {

      $log.debug('=== vm.role ===');
      $log.debug(vm.role);

      var onSuccess = function() {
        $log.debug('login success');

        // if the next state is login, it will go to profile state
        if ($rootScope.toState.name == 'login') {
          $state.go('profile');
        } else {

          // go to next with parameters
          if (angular.isDefined($rootScope.toStateParams)) {
            $state.go($rootScope.toState.name, $rootScope.toStateParams);
          } else {
            $state.go($rootScope.toState.name);
          }
        }
      };
      var onFailure = function(response) {
        $log.debug('login error');

        if (response.status == 401) {
          vm.alert.push({
            type: 'danger',
            message: ['']
          });
        } else {
          vm.alert.push({
            type: 'danger',
            message: ['']
          });
        }

        $log.debug('vm.alter');
        $log.debug(vm.alter);
      };

      userAuthentication
        .login(vm.credentials, vm.role)
        .then(onSuccess)
        .catch(onFailure);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('EmailVerificationController', EmailVerificationController);

  /** @ngInject */
  function EmailVerificationController($log, $location, vmsClient, authPrinciple, $state, $rootScope, $timeout) {
    var vm = this;
    vm.verification = true;
    vm.verificationSuccess = false;
    vm.verificationStatus = vm.verification || vm.verificationSuccess;

    $log.debug("vm.verification " + vm.verification);
    $log.debug("vm.verificationSuccess " + vm.verificationSuccess);
    $log.debug("vm.verificationStatus " + vm.verificationStatus);

    vm.volunteer = $location.search();

    if (angular.isDefined(vm.volunteer.email) && angular.isDefined(vm.volunteer.verification_token)) {

      angular.element(document).ready(verify());
    } else {
      $log.debug("verification false");
      vm.verification = false;
      vm.verificationStatus = vm.verification || vm.verificationSuccess;

      $log.debug("vm.verificationStatus " + vm.verificationStatus);
    }

    function verify() {
      $log.debug("EmailVerificationController verify()");

      $log.debug("email = " + vm.volunteer.email);
      $log.debug("verification_token = " + vm.volunteer.verification_token);

      var successCallback = function(response) {
        $log.debug('emailVerification success');
        $log.debug(response);

        vm.verification = false;
        vm.verificationSuccess = true;
        vm.verificationStatus = vm.verification || vm.verificationSuccess;

        $timeout(function() {
          $state.go('profile')
        }, 5000);
      };

      var failureCallback = function(response) {
        $log.debug('emailVerification error');
        $log.debug(response);

        vm.verification = false;
        vm.verificationStatus = vm.verification || vm.verificationSuccess;
      };

      vmsClient.emailVerification(vm.volunteer.email,
        vm.volunteer.verification_token)
        .then(successCallback)
        .catch(failureCallback);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('EditVolunteerProfileController', EditVolunteerProfileController);

  /** @ngInject */
  function EditVolunteerProfileController($log, userProfile, cities) {
    var vm = this;
    vm.cities = cities;

    vm.datePicker = {
      mode: 'year',
      dateOptions: {
        datepickerMode: 'year',
        minMode: "'year'",
        // minDate: "minDate",
        showWeeks: "false"
      },
      status: {
        opened: false
      },
      format: 'yyyy'
    };

    vm.openDatePicker = function($event) {
      $event.preventDefault();
      $event.stopPropagation();
      vm.datePicker.status.opened = true;
    };

    // In the begining, it gets the volunteer's profile
    angular.element(document).ready(getProfile());

    function getProfile() {
      var doneCallbacks = function(value) {
          vm.profile = value.data;

          $log.debug(vm.profile);
        },
        failCallbacks = function(response) {
          $log.debug(response);
        };

      userProfile
        .get()
        .then(doneCallbacks, failCallbacks);
    }

    vm.updateAboutAndContact = function() {
      var onSuccess = function(value) {
        $log.debug(value);
        vm.profile = value.data;
      };

      $log.debug(vm.profile);

      userProfile
        .update(vm.profile)
        .then(onSuccess);
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('EditSkillEquipmentController', EditSkillEquipmentController);

  function EditSkillEquipmentController(userEquipment, userSkill, arrayHelpersService, $scope, $log) {
    var vm = this;
    vm.equipment = [];
    vm.skills = [];
    vm.skillCandidatedKeywords = [];
    vm.equipmentCandidatedKeywords = [];
    $scope.originSkills = [];
    $scope.originEquipment = [];

    angular.element(document).ready(getSkills);
    angular.element(document).ready(getEquipment);

    function getSkills() {
      var onSuccess = function(value) {
        $log.debug(value.skills);

        vm.skills = value.skills;
        $scope.originSkills = angular.copy(value.skills);

        $log.debug('### $scope.originSkills ###');
        $log.debug($scope.originSkills);
      };

      var onFailure = function(data) {
        /**
         * TODO: show error msg
         */
      };

      userSkill.getAll()
        .then(onSuccess)
        .catch(onFailure);
    }

    function getEquipment() {
      var onSuccess = function(value) {
        $log.debug(value.equipment);

        vm.equipment = value.equipment;
        $scope.equipment = angular.copy(value.equipment);

        $log.debug('### $scope.equipment ###');
        $log.debug($scope.equipment);
      };

      var onFailure = function(data) {
        /**
         * TODO: show error msg
         */
      };

      userEquipment
        .getAll()
        .then(onSuccess)
        .catch(onFailure);
    }

    vm.refreshSkillCandidatedKeywords = function(skill) {
      $log.debug('search skill keyword');
      $log.debug(skill);

      var onSuccess = function(result) {
        return result;
      };

      return userSkill
        .getCandidatedKeywords(skill)
        .then(onSuccess);
    };

    vm.refreshEquipmentCandidateKeywords = function(equipment) {
      $log.debug('search equipment keyword');
      $log.debug(equipment);

      var onSuccess = function(result) {
        return result;
      };

      return userEquipment
        .getCandidatedKeywords(equipment)
        .then(onSuccess);
    };

    vm.updateSkills = function() {
      $log.debug('updateSkills()');
      $log.debug(vm.skills);

      var onSuccess = function() {
        getSkills();
      };
      var onFailure = function(data) {
        /**
         * TODO: show error msg
         */
      };

      $log.debug('$scope.originSkills');
      $log.debug($scope.originSkills);

      var existingIndexes = arrayHelpersService.getExistingIndexes($scope.originSkills, vm.skills);

      $log.debug('existingIndexes');
      $log.debug(existingIndexes);

      userSkill.update(vm.skills, existingIndexes)
        .then(onSuccess)
        .catch(onFailure);
    };

    vm.updateEquipment = function() {
      $log.debug('updateEquipment()');
      $log.debug(vm.equipment);

      var onSuccess = function() {
        getSkills();
      };
      var onFailure = function(data) {
        /**
         * TODO: show error msg
         */
      };

      $log.debug('$scope.originEquipment');
      $log.debug($scope.originEquipment);

      var existingIndexes = arrayHelpersService.getExistingIndexes($scope.originEquipment, vm.equipment);

      $log.debug('existingIndexes');
      $log.debug(existingIndexes);

      userEquipment
        .update(vm.equipment, existingIndexes)
        .then(onSuccess)
        .catch(onFailure);
    };
  }
})();

